body{
    direction: rtl;
    margin-top: 80px;
    text-align: right;
}
h2,
h3{
    font-weight: unset;
}
.main-nav{
    padding: 40px 0;
    background-color: #fff;
    transition: .3s linear;
    &.scrolled{
        padding: 20px 0;
        box-shadow: 0 0 5px rgba(#000,.2)
    }
    .nav-container{
        justify-content: space-between;
        align-items: center;
        ul{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 0;
            padding-inline-start: 0;
            li{
                &:not(:last-child){
                    margin-left: 30px;
                }
                a{
                    font-size: 16px;
                    color: rgb(15, 36, 96);
                    font-family: "ITC-W23";
                    text-decoration: none;
                    transition: .25s ease-in-out;
                    text-shadow: 0 30px 0px transparent;
                    &:hover{
                        text-shadow: 0 0px 0px #1580f1;
                        color: transparent;
                    }
                }
            }
        }
    }
}
.btn-container{
    text-align: center;
}
.main-btn {
    background-color: #1580f1;
    width: 150px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-family: "ITC-W23Bd";
    color: #fff;
    border-radius: 100px;
    position: relative;
    transition: .5s;
    backface-visibility: hidden;
    i{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 23px;
        font-size: 10px;
        transition: .5s;
        &:nth-of-type(2){
            opacity: 0;
            left: 16px;
        }
    }
    &:hover{    
        text-decoration: none;
        color: #fff;
        transform: translateX(-6px);
        i{
            opacity: 1;
            animation: color-anim 1s infinite 0.4s;
            &:nth-of-type(2){
                animation: color-anim 1s infinite 0.6s;
            }
        }
    }
    &:active{
        transform: translate(-6px , 3px);
    }

}
@keyframes color-anim {
    0% {
        color: white;
    }
    50% {
        color:rgba(#fff,.7);
    }
    100% {
        color: white;
    }
}
.section-one{
    padding: 157px 0;
    overflow: hidden;
    position: relative;
    .ads-con{
        h2{
            font-size: 30px;
            font-family: "ITC-W23Md";
            color: rgb(15, 36, 96);
            margin-bottom: 48px;
            position: relative;
            span{
                display: block;
            }
            &:after{
                content: '';
                background-color: rgb(21, 128, 241);
                width: 60px;
                height: 3px;
                position: absolute;
                right: -74px;
                top: 26px;
            }
            
        }
        p{
            font-size: 17px;
            color: rgb(101, 115, 153);
            font-family: "ITC-W23Lt";
            margin-bottom: 50px;
        }
    }
    .img-con{
        position: relative;
        width: 100%;
        height: 100%;
        .img{
            position: absolute;
            left: 0;
            top: 0;
            width: max-content;
            &-1{
                top: -84px;
                left: -45px;
            }
            &-2{
                top: -24px;
                left: 160px;
            }
            &-3{
                top: 135px;
                left: 129px;
            }
        }
    }
}
.pt{
    img{
        position: absolute;
        opacity: 0;
        right: -20%;
        &:nth-of-type(1){
            top: 10%;
            animation: ptanimte 50s 1s linear infinite alternate ;
        }
        &:nth-of-type(2){
            top: 30%;
            animation: ptanimte 60s 10s  linear infinite alternate ;
            
        }
        &:nth-of-type(3){
            top: 35%;
            animation: ptanimte 60s 20s linear infinite alternate ;
            
        }
        &:nth-of-type(4){
            top: 40%;
            animation: ptanimte 50s 5s linear infinite alternate ;
            
        }
        &:nth-of-type(5){
            top: 50%;
            animation: ptanimte 40s 10s linear infinite alternate ;
            
        }
        &:nth-of-type(6){
            top: 80%;
            animation: ptanimte 60s 15s linear infinite alternate ;
            
        }
        &:nth-of-type(7){
            top: 60%;
            animation: ptanimte 50s  linear infinite alternate ;

        }
        &:nth-of-type(8){
            top: 75%;
            animation: ptanimte 40s 15s linear infinite alternate ;
            
        }

    }
}
@keyframes ptanimte {
    0%{
        opacity: 1;
        right: -20px;
        opacity: 0;
    }
    50%{
        opacity: 1;

        right: 50%;
        transform: rotate(500deg);
    }
    100%{
        right: 100%;
    }
}
.section-two{
    position: relative;
    background-image: url('../images/bageBake.png');
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    overflow: hidden;
    padding: 174px 0;
    .two-container
    {
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
    }
    .bottom-two{
        padding: 60px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }
    .card-con{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        &-1{
            margin:auto;
            img:last-of-type{
                top: 23%;
                left: 12.5%;
                animation: img-con-1 2s ease-in-out infinite alternate ;
            }

        }
        &-2{
            padding-top: 62px;
            padding-left: 105px;
            img:last-of-type{
                top: 17%;
                left: 32%;
                animation: img-con-2 2s ease-in-out infinite alternate ;
            }

        }
        &-3{
            img:last-of-type{
                top: 30%;
                left: 30.5%;
                animation: img-con-3 2s ease-in-out infinite ;

            }

        }
        &-4{
            img:last-of-type{
                top: 13%;
                left: 0.5%;
                animation: img-con-4 2s ease-in-out infinite alternate ;
            }
            
        }
        &-5{
            padding-top: 62px;
            padding-right: 105px;

            img:last-of-type{
                top: 11%;
                left: 12.5%;
                animation: img-con-5 2s ease-in-out infinite alternate ;
            }
        }
        .img-con{
            position: relative;
            width: max-content;
            img:last-of-type{
                position: absolute;
            }
        }
        .titel{
            font-size: 17px;
            color: rgb(15, 36, 96);
            font-family: "ITC-W23";
            margin-top: -30px;
            padding-right: 15px;
            span{
                display: block;
            }
        }
        
    }
}
@keyframes img-con-1 {
    0%{
        transform: translate(1px, -1px) rotate(-3deg);
    }
    100%{
        transform: translate(-1px, -1px) rotate(3deg);
    }
}
@keyframes img-con-2 {
    0%{
        transform: translate(0px, 0px) rotate(0);
    }
    100%{
        transform: translate(0px, 0px) rotate(-15deg);
    }
}
@keyframes img-con-3 {
    0%{
        transform: translate(0px, 0);
    }
    100%{
        transform: translate(0px, 14px);
        opacity: 0;
    }
}
@keyframes img-con-4 {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    100%{
        transform: translate(-1px, 4px) rotate(7deg);
    }
}
@keyframes img-con-5 {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    100%{
        transform: translate(4px, 4px) rotate(-10deg);
    }
}
