            justify-content: center;
            align-items: center;
            width: 300px;
            height: auto;
            position: relative;
        }
        .arr{
            position: absolute;
            left: -18%;
            top: 33%;
        }
        .one{
            img:nth-of-type(2){
                right: 19%;
                top: 35%;
                animation: img-three-1 2s ease-in-out infinite alternate ;
            }
        }
        .two{
            padding-bottom: 27px;
            img:nth-of-type(2){
                left: -47%;
                top: 46%;
                animation: img-three-2 2s ease-in-out infinite alternate ;

            }

        }
        .threeimg{
            img:nth-of-type(2){
                left: 0;
                top: -128%;
                animation: img-three-3 2s ease-in-out infinite alternate ;

            }
            .arr{
                top: 0;
            }
        }
        .four{
            img:nth-of-type(2){
                left: 61%;
                top: -15%;
                opacity: 0;
                animation: img-three-4 4s  ease-in-out infinite  ;

            }
            img:nth-of-type(3){
                left: 47%;
                top: -34%;
                opacity: 0;
                animation: img-three-4 4s  ease-in-out infinite  ;

            }
            img:nth-of-type(4){
                left: 25%;
                top: -8%;
                opacity: 0;
                animation: img-three-4 4s  ease-in-out infinite  ;

            }

        }
    } 
    .btn-container{
        display: flex;
        margin-top: 65px;
        a{
            margin: auto;
        }
    }
}
@keyframes img-three-1 {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    100%{
        transform: translate(2px, 4px) rotate(-14deg);
    }
}
@keyframes img-three-2 {
    0%{
        transform: scale3d(1, 1, 1);
    }
    100%{
        transform: scale3d(1.2, 1.2, 1.2);
    }
}
@keyframes img-three-3 {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    100%{
        transform: translate(5px, 5px) rotate(-10deg);
    }
}
@keyframes img-three-4 {
    0%{
        transform: translate(0, 0) rotate(0);
        opacity: 1;
    }
    100%{
        transform: translate(0, 43px) rotate(26deg);
        opacity: 0;
    }
}
